import svg4everybody from 'svg4everybody';
import $ from 'jquery';

$(() => {
	function initModals() {
		console.log('modals inited');
		// Show modal
		$('[data-link="modal"]').on('click', function () {
			const link = $(this).attr('href') || $(this).data('href');
			const modal = $(link);

			$(modal).addClass('active');
		});
		// Hide modal on cross click
		$('.modal').on('click', '.close', function () {
			$(this).closest('.modal').removeClass('active');
		});
		// Hide modal on empty place click
		$(document).on('click', function (e) {
			const target = e.target;

			if ($(target).hasClass('modal')) {
				$('.modal').removeClass('active');
			}
		});
	}
	function handleScroll(e) {
		// let megaMenus = $('.js-mega-menu');
		let megaMenusOffsetY = $('.header__bottom').scrollTop();

		console.log(megaMenusOffset);
	}
	function initMegaMenu() {
		console.log('mega-menus inited');
		let megaMenus = $('.js-mega-menu');

		let scrolled;
		$(window).on('scroll', handleScroll);
	}

	$(document).ready(() => {
		svg4everybody();
		initModals();
		initMegaMenu();
	});

});



